<template>
  <div class="one">
    <cardDragger
      :data="cardList"
      :colNum="3"
      :cardOutsideWidth="565"
      :cardInsideWidth="500"
      @startDrag="startDrag"
      @swicthPosition="swicthPosition"
      @finishDrag="finishDrag"
    >
      <!-- template v-slot:header="slotProps"
       header
      slotPropsdata-->
      <!-- <template v-slot:header="slotProps">
    <div class="topMenuBox" style="border:1px solid red">
      <div class="menuTitle" v-if="slotProps.item.name">{{slotProps.item.name}}</div>
      <div class="menuTitle" v-else>  </div>
    </div>
      </template>-->
      <!-- v-slot:content-->
      <template v-slot:content="slotProps">
        <!-- <div class="insideData">{{slotProps.item.name}}</div> -->
        <div :id="slotProps.item.name" class="charts"></div>
      </template>
    </cardDragger>
  </div>
</template>

<script>
import { cardDragger } from "carddragger";
import Highcharts from "highcharts/highstock";
import HighchartsMore from "highcharts/highcharts-more";
import HighchartsDrilldown from "highcharts/modules/drilldown";
import Highcharts3D from "highcharts/highcharts-3d";
HighchartsMore(Highcharts);
HighchartsDrilldown(Highcharts);
Highcharts3D(Highcharts);
export default {
  name: "One",
  data() {
    return {
      cardList: [
        {
          positionNum: 1,
          name: "chart1",
          id: "card1"
        },
        {
          positionNum: 2,
          name: "chart2",
          id: "card2"
        },
        {
          positionNum: 3,
          name: "chart3",
          id: "card3"
        }
      ]
    };
  },
  props: {
    msg: String
  },
  mounted() {
    this.initChart1();
    this.initChart2();
    this.initChart3();
  },
  methods: {
    startDrag(event, id) {
      console.log(event);
      console.log(id);
    },
    swicthPosition(oldPositon, newPositon, originItem) {
      console.log(oldPositon); // 
      console.log(newPositon); // 
      console.log(originItem); // 
    },
    finishDrag(oldPositon, newPositon, originItem) {
      console.log(oldPositon); // 
      console.log(newPositon); // 
      console.log(originItem); // 
    },

    initChart1() {
      this.chart = new Highcharts.Chart(document.getElementById("chart1"), {
        title: {
          text: "2010 ~ 2016 "
        },
        credits: {
          enabled: false //
        },
        subtitle: {
          text: "thesolarfoundation.com"
        },
        yAxis: {
          title: {
            text: ""
          }
        },
        legend: {
          layout: "vertical",
          align: "right",
          verticalAlign: "middle"
        },
        plotOptions: {
          series: {
            label: {
              connectorAllowed: false
            },
            pointStart: 2010
          }
        },
        series: [
          {
            name: "",
            data: [43934, 52503, 57177, 69658, 97031, 119931, 137133, 154175]
          },
          {
            name: "",
            data: [24916, 24064, 29742, 29851, 32490, 30282, 38121, 40434]
          },
          {
            name: "",
            data: [11744, 17722, 16005, 19771, 20185, 24377, 32147, 39387]
          },
          {
            name: "",
            data: [null, null, 7988, 12169, 15112, 22452, 34400, 34227]
          },
          {
            name: "",
            data: [12908, 5948, 8105, 11248, 8989, 11816, 18274, 18111]
          }
        ],
        responsive: {
          rules: [
            {
              condition: {
                maxWidth: 500
              },
              chartOptions: {
                legend: {
                  layout: "horizontal",
                  align: "center",
                  verticalAlign: "bottom"
                }
              }
            }
          ]
        }
      });
    },
    initChart2() {
      this.chart = new Highcharts.Chart(document.getElementById("chart2"), {
        chart: {
          type: "column"
        },
        title: {
          text: ""
        },
        credits: {
          enabled: false //
        },
        xAxis: {
          categories: ["", "", "", "", ""]
        },
        yAxis: {
          allowDecimals: false,
          min: 0,
          title: {
            text: ""
          }
        },
        tooltip: {
          formatter: function() {
            return (
              "<b>" +
              this.x +
              "</b><br/>" +
              this.series.name +
              ": " +
              this.y +
              "<br/>" +
              ": " +
              this.point.stackTotal
            );
          }
        },
        plotOptions: {
          column: {
            stacking: "normal"
          }
        },
        series: [
          {
            name: "",
            data: [5, 3, 4, 7, 2],
            stack: "male" // stack 
          },
          {
            name: "",
            data: [3, 4, 4, 2, 5],
            stack: "male"
          },
          {
            name: "",
            data: [2, 5, 6, 2, 1],
            stack: "female"
          },
          {
            name: "",
            data: [3, 0, 4, 4, 3],
            stack: "female"
          }
        ]
      });
    },
    initChart3() {
      this.chart = new Highcharts.Chart(document.getElementById("chart3"), {
        chart: {
          type: "pie",
          options3d: {
            enabled: true,
            alpha: 45
          }
        },
        title: {
          text: ""
        },
        credits: {
          enabled: false //
        },
        subtitle: {
          text: ""
        },
        plotOptions: {
          pie: {
            innerSize: 100,
            depth: 45
          }
        },
        series: [
          {
            name: "",
            data: [
              ["", 8],
              ["", 3],
              ["", 1],
              ["", 6],
              ["", 8],
              ["", 4],
              ["", 4],
              ["", 1],
              [" ()", 1]
            ]
          }
        ]
      });
    }
  },
  components: {
    cardDragger
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
h3 {
  margin: 40px 0 0;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
.charts {
  width: 100%;
  height: 600px;
}
</style>
